.bread-crumb{background-image:url("https://themebeyond.com/html/geco/Geco/img/bg/breadcrumb_bg.jpg");padding-top:20rem;padding-bottom:20rem;background-position:center;background-size:cover}.bread-crumb__container{width:100%;padding-right:15px;padding-left:15px;margin-right:auto;margin-left:auto;text-align:center}@media only screen and (min-width: 1200px){.bread-crumb__container{max-width:1230px}}.bread-crumb__container h2{color:#fff;font-size:7rem;text-transform:uppercase;margin-bottom:15px;line-height:.8}.bread-crumb__container span{color:#e4a101}.bread-crumb__container .address{margin-top:5rem}.bread-crumb__container .address ol{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;padding:0;margin-bottom:0;list-style:none;background-color:rgba(0,0,0,0);border-radius:0;justify-content:center}.bread-crumb__container .address ol li{text-transform:uppercase;font-weight:600;list-style:none}.bread-crumb__container .address ol li:hover a{color:#e4a101}.bread-crumb__container .address ol li+li::before{display:inline-block;padding-right:.5rem;padding-left:.5rem;color:#fff;content:"";font-weight:600;font-family:"Font Awesome 5 Free";font-size:12px}.bread-crumb__container .address ol li a{color:#d4d4d4;text-decoration:none}.bread-crumb__container .address ol li:last-child a{color:#f2a900}